li{
    list-style-type: none;
}
.contenedorMsg{
    width: 75vh;
    display: flex;
    flex-direction: column;
    margin: auto;
}

.derecha{
    align-self: flex-end;
}
.derechaMsg{   
    background-color: #f8e896;
    border: 1px solid #dfd087;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
}

.izquierda{
    align-self: flex-start;
}


.izquierdaMsg{
    border: 1px solid #97C6E3;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    background-color: #A8DDFD;
}